'user client'
import { useState } from "react";

export default function SideBar() {
  const [collapseLayouts, setCollapseLayouts] = useState(false);
  const [collapsePages, setCollapsePages] = useState(false);
  const [collapseAuth, setCollapseAuth] = useState(false);
  const [collapseError, setCollapseError] = useState(false);

  return (
    <aside className="w-64 h-screen bg-gray-900 text-white flex flex-col">
      <div className="p-4 text-lg font-semibold border-b border-gray-700">
        Start Bootstrap
      </div>
      <nav className="flex-1 overflow-y-auto p-4 space-y-2 text-sm">
        <div className="text-gray-400 uppercase text-xs">Core</div>
        <a href="index.html" className="flex items-center space-x-2 hover:bg-gray-800 p-2 rounded">
          <i className="fas fa-tachometer-alt" />
          <span>Dashboard</span>
        </a>

        <div className="text-gray-400 uppercase text-xs mt-4">Interface</div>
        {/* Layouts */}
        <button
          onClick={() => setCollapseLayouts(!collapseLayouts)}
          className="w-full flex justify-between items-center hover:bg-gray-800 p-2 rounded"
        >
          <span className="flex items-center space-x-2">
            <i className="fas fa-columns" />
            <span>Layouts</span>
          </span>
          <i className={`fas fa-angle-down transform transition ${collapseLayouts ? "rotate-180" : ""}`} />
        </button>
        {collapseLayouts && (
          <div className="ml-6 space-y-1">
            <a href="layout-static.html" className="block hover:underline">Static Navigation</a>
            <a href="layout-sidenav-light.html" className="block hover:underline">Light Sidenav</a>
          </div>
        )}

        {/* Pages */}
        <button
          onClick={() => setCollapsePages(!collapsePages)}
          className="w-full flex justify-between items-center hover:bg-gray-800 p-2 rounded"
        >
          <span className="flex items-center space-x-2">
            <i className="fas fa-book-open" />
            <span>Pages</span>
          </span>
          <i className={`fas fa-angle-down transform transition ${collapsePages ? "rotate-180" : ""}`} />
        </button>
        {collapsePages && (
          <div className="ml-6 space-y-1">
            {/* Auth */}
            <button
              onClick={() => setCollapseAuth(!collapseAuth)}
              className="w-full flex justify-between items-center"
            >
              <span>Authentication</span>
              <i className={`fas fa-angle-down transform transition ${collapseAuth ? "rotate-180" : ""}`} />
            </button>
            {collapseAuth && (
              <div className="ml-4 space-y-1">
                <a href="login.html" className="block hover:underline">Login</a>
                <a href="register.html" className="block hover:underline">Register</a>
                <a href="password.html" className="block hover:underline">Forgot Password</a>
              </div>
            )}

            {/* Error */}
            <button
              onClick={() => setCollapseError(!collapseError)}
              className="w-full flex justify-between items-center"
            >
              <span>Error</span>
              <i className={`fas fa-angle-down transform transition ${collapseError ? "rotate-180" : ""}`} />
            </button>
            {collapseError && (
              <div className="ml-4 space-y-1">
                <a href="401.html" className="block hover:underline">401 Page</a>
                <a href="404.html" className="block hover:underline">404 Page</a>
                <a href="500.html" className="block hover:underline">500 Page</a>
              </div>
            )}
          </div>
        )}

        <div className="text-gray-400 uppercase text-xs mt-4">Addons</div>
        <a href="charts.html" className="flex items-center space-x-2 hover:bg-gray-800 p-2 rounded">
          <i className="fas fa-chart-area" />
          <span>Charts</span>
        </a>
        <a href="tables.html" className="flex items-center space-x-2 hover:bg-gray-800 p-2 rounded">
          <i className="fas fa-table" />
          <span>Tables</span>
        </a>
      </nav>
      <div className="p-4 border-t border-gray-700 text-sm text-gray-400">
        <div>Logged in as:</div>
        <div>Start Bootstrap</div>
      </div>
    </aside>
  );
}
